  line-height: 1.5;
}

.example code {
  color: #fff;
  background-color: #ff6b0a;
  border-radius: 2px;
  padding: 2px 6px;
  font-family: monospace;
  font-size: 16px;
  font-weight: 700;
}

.example .background {
  height: 240px;
  background-image: url("logo.268195b3.png");
  background-position: center;
  background-size: cover;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

:root {
  --maine-background-color: #fff;
  --secondary-background-color: #f5f4fa;
  --complementary-background-color: #2f303a;
  --maine-text-color: #757575;
  --secondary-text-color: #212121;
  --complementary-text-color: #fff;
  --accent-text-color: #2196f3;
  --extra-accent-text-color: #277cc2;
  --individual-text-color: rgba(255, 255, 255, .6);
  --logo-dicor-color: #000;
  --portfolio-card-border-color: #eee;
  --portfolio-card-set-gap: 30px;
  --timing-function: cubic-bezier(.4, 0, .2, 1);
}

.link {
  text-decoration: none;
}

.link:hover, .link:focus {
  color: var(--accent-text-color);
}

.visually-hidden, .form__checkbox-input {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.current-position {
  color: var(--accent-text-color);
  position: relative;
}

.current-position:after {
  content: "";
  width: 100%;
  height: 4px;
  background-color: var(--accent-text-color);
  border-radius: 2px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.container {
