.backdrop {
  width: 100%;
  height: 100%;
  z-index: 2;
  opacity: 1;
  transition: opacity 250ms var(--timing-function);
  background: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

@media screen and (max-width: 480px) {
  .backdrop {
    padding: 93px 15px;
  }
}

.backdrop.is-hidden {
  pointer-events: none;
  opacity: 0;
  visibility: hidden;
  transition: opacity 250ms var(--timing-function), visibility 250ms var(--timing-function);
}

.backdrop.is-hidden .modal-window {
  transform: translate(-50%, -50%)scale(.8);
}

.modal-window {
  background-color: var(--maine-background-color);
  transition: transform 350ms var(--timing-function), scale 350ms var(--timing-function);
  border-radius: 4px;
  padding: 40px;
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

@media screen and (min-width: 480px) {
